@font-face {
  font-family: "Poppins";
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/Poppins-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 500;
  font-style: normal;
  src: url("../../fonts/Poppins-Medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 600;
  font-style: normal;
  src: url("../../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  font-weight: 700;
  font-style: normal;
  src: url("../../fonts/Poppins-Bold.woff2") format("woff2");
  font-display: swap;
}
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.page {
  background-color: hsl(158, 41%, 95%);
  font-family: "Poppins";
  padding-top: 1.875em;
}

button {
  font-family: "Poppins";
}

.container {
  max-width: 68.75em;
  margin: 0 auto;
}

.header {
  margin-bottom: 7.5em;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-content__button {
  font-size: 1em;
  background-color: hsl(144, 44%, 50%);
  border: none;
  font-weight: 500;
  border-radius: 1.25em;
  padding: 0.5em 1.5em;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
}

.logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.logo__img {
  margin-right: 0.5em;
}
.logo__text {
  color: hsl(0, 0%, 18%);
  font-weight: 500;
}

.menu {
  display: flex;
  gap: 3.125em;
}
.menu__link {
  position: relative;
  color: hsl(0, 0%, 29%);
  text-decoration: none;
}
.menu__link:hover::after, .menu__link--active::after {
  content: "";
  width: 1.3125em;
  height: 2px;
  position: absolute;
  bottom: -0.625em;
  left: 50%;
  transform: translateX(-50%);
  background-color: hsl(21, 95%, 70%);
}